<template>
  <div class="card">
    <div class="card-header">Exam02TemplateRefs</div>
    <div class="card-body">
      <div>
        <h6>[DOM 요소 참조]</h6>
        <p>data: {{ data }}</p>
        <button @click="setInputValue" class="btn btn-secondary btn-sm mr-2">데이터 변경</button>
        <input type="text" ref="inputTag" name="mid" />
      </div>
      <hr />
      <div>
        <h6>[자식 컴포넌트 참조] : 뷰에서는 이런 코드는 피하는게 좋음(가장 안좋은 코드임)</h6>
        <p>child data: {{ (childRef !==null)?childRef.data:""}}</p>
        <p>
          <button @click="changeChildData" class="btn btn-secondary btn-sm mr-2">자식 데이터 변경</button>
          <button @click="callChildMethod" class="btn btn-secondary btn-sm mr-2">자식 메소드 호출</button>
        </p>
        <Child ref="childRef"/>
      </div>
    </div>
  </div>
</template>

<script setup>
import Child from "./Child";
import { ref, onMounted } from "vue";

const data = ref("summer");
const inputTag = ref(null);

const childRef = ref(null);

onMounted(() => {
  console.log(inputTag.value);
  inputTag.value.focus();

  console.log(childRef);
});

function setInputValue() {
  inputTag.value.value = data.value;
}

function changeChildData(){
  childRef.value.data += 1;
}

function callChildMethod(){
  childRef.value.method();
}
</script>

<style scoped></style>
